// Common styles of the project





body{
    font-family: "open sans", sans-serif;
}


.clearfix {
    overflow: auto;
    zoom: 1;
}


/*
  ====================================================================
  Header
  ====================================================================
*/
.tablet-header{
    display: none;
}
.main-header {
    min-height: 170px;
    //background: $grey;
    &__row_above {
        min-height: 50px;
        background: #f8f8f8;
        ul {
            height: 30px;
        }
        li {
            float: left;
            padding-left: 15px;
        }
    }
    a {
         text-decoration: none;
         color: $black;

     }

    &__language {
        padding-top: 15px;
        font-size: 13px;
        width: 20%;
        float: left;
        li {
            &:first-child {
                a {
                    border-bottom: 3px solid $red;
                }
            }
        }
    }
    &__phone {
        padding-top: 15px;
        width: 35%;
        float: left;
    }
    select {
        border: none;
        background: #f8f8f8;
        font-size: 15px;
    }
    &__social {
        padding-top: 15px;
        width: 12%;
        float: left;
        height: 20px;
    }
    img{
        transition: $transition;
        &:hover{
            transition: $transition;
            transform: scale(0.85);
        }
    }
    .face{
        padding-left: 15px;
    }
    &__search {
        padding-top: 25px;
        width: 15%;
        float: left;
        display: block;
        position: relative;
        top: 10px;
        transform: translate(0, -50%);
        .search-field {
            cursor: pointer;
            width: 22px;
            height: 22px;
            font-family: "futura-demic-reg", Arial, "Helvetica Neue", Helvetica, sans-serif;
            font-size: 12px;
            line-height: 44px;
            color: $black;
            background: none;
            letter-spacing: 1.2px;
            background: transparent;
            text-transform: uppercase;
            border: 2px solid transparent;
            outline: 0;
            padding: 0 40px 0 0;
            box-sizing: border-box;
            transition-duration: 0.3s;
            transition-timing-function: linear;
            &:focus {
                width: 180px;
                border: 2px solid $black;
                padding: 0 15px 0 40px;
            }
            &:active {
                width: 180px;
                border: 2px solid $black;
                padding: 0 15px 0 40px;
            }
        }
    }
    &__enter {
        padding-top: 15px;
        font-size: 14px;
        float: right;

    }
    &__row_center {
        min-height: 70px;
        background: white;
    }
    &__logo{
        width: 110px;
        height: 40px;
        background: $red;
        float: left;
        position: relative;
        top: 15px;
    }
    &__menu {
        z-index: 5;
        ul{
            padding-top: 30px;
            padding-left: 10%;
        }
        li {
            float: left;
            list-style-type: none;
            &:last-child{
                float: right;
                //padding-right: 3%;
                a{
                    background: url("../img/content/basket.png") no-repeat;
                    color: $red;
                    &:hover {
                        border-bottom: none;
                        padding-bottom: 10px;
                        //text-decoration: none;
                        border: transparent;

                    }
                    span{
                        position: relative;
                        left: 25px;
                        text-decoration: none;
                    }

                }
            }
        }
        a {
            display: block;
            margin: 0px 70px 0 0px;
            font-size: 14px;
            transition: $transition;
            border-bottom: 3px solid transparent;
            &:hover {
                transition: $transition;
                border-bottom: 3px solid $black;
                padding-bottom: 10px;
            }
        }
    }


    &__row_below{
        min-height: 50px;
        background: black;
        ul{
            height: 50px;
        }
        li{
            height: 50px;
            float: left;
            width: 20%;
            text-align: center;
            font-size: 13px;
            &:first-child{
                background: $red;
                transition: $transition;

                a {
                    background: url("../img/content/gift.png") no-repeat;
                    float: none;
                    margin-left: -45px;
                    padding: 12px 0;
                    &:hover {
                        //background: $white;
                        background: url("../img/content/gift_black.png") no-repeat;
                    }
                }
                span{
                    position: relative;
                    left: 35px;
                    text-decoration: none;
                }
                &:hover{
                    //opacity: 0.9;
                    border-radius: 30px;

                    a{
                        color: $black;
                        transform: scale(1.2);
                        transition: $transition;
                    }

                }
            }
            &:nth-child(4){
                a{
                    border-bottom: 2px solid $white;
                    padding-bottom: 5px;
                    transition: $transition;
                    &:hover{
                        border-bottom: 2px solid $red;
                        transition: $transition;
                    }
                }
            }
        }

        a{
            //padding-top: 20px;
            position: relative;
            top: 20px;
            color: $white;
            transition: $transition;
            float: right;
            &:hover{
                color: $red;
                transition: $transition;
            }
        }
    }
}


