/*
  ====================================================================
  Main
  ====================================================================
*/
.start-slider{
    //background: url(../img/content/main_slider.png) no-repeat;
    min-height: 472px;
    width: 100%;
    color: $white;
    background: #86b7c8;
    &__item{
        float: left;
        width: 50%;

    }
    &__title{
        padding-top: 100px;
        padding-left: 100px;
        width: 530px;
        margin: 0 auto;
        position: relative;
        z-index: 2;
    }
    h1{
        font-size: 86px;
        font-weight: bold;
    }
    h5{
        font-size: 16px;
        padding: 20px 103px 0 0;
        line-height: 23px;
    }
    &__button {
        width: 250px;
        padding-top: 30px;
        .btn {
            opacity: 0; // for animation
            background: transparent;
            font-size: 13px;
            background-size: cover;
            color: $white;
            padding: 0 30px;
            text-decoration: none;
            min-height: 49px;
            text-align: center;
            line-height: 49px;
            margin-bottom: 20px;
            position: relative;
            cursor: pointer;
            display: inline-block;
            z-index: 2;
            border: 3px solid $white;
            transition: $transition;
            &:before{
                content: "";
                position: absolute;
                z-index: -1;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                border-radius: 30px;
                background: $red;
                transform: scaleY(0);
                transform-origin: 50%;
                transition: $transition;
            }
            &:hover{
                background: inherit;
                font-weight: bold;
                color: $white;
                border-radius: 30px;
                border: 3px solid $white;
                transition: $transition;
                fill: $purple;
            }
        }
        .btn:hover:before, .btn:focus:before, .btn:active:before {
            transform: scaleY(1);
        }
    }
    img{
        z-index: 1;
        height: 472px;
        padding-left: 100px;
    }

    /////////main-slider////////////////
    .bx-wrapper .bx-viewport {
        transform: translatez(0);
        //height: 100% !important;
    }

    .bx-wrapper .bx-pager,
    .bx-wrapper .bx-controls-auto {
        position: absolute;
        bottom: 15%;
        width: 100%;
        //height: 100%;
    }

    /* PAGER */

    .bx-wrapper .bx-pager {
        text-align: center;
        font-size: .85em;
        font-weight: bold;
        padding-top: 20px;

    }

    .bx-wrapper .bx-pager .bx-pager-item,
    .bx-wrapper .bx-controls-auto .bx-controls-auto-item {
        display: inline-block;
        *zoom: 1;
        *display: inline;
        &:before {
            content: '';
            position: absolute;
            width: 14px;
            height: 14px;
            background-color: transparent;
            border: 2px solid $white;
            border-radius: 100%;
            opacity: 0.8;
            z-index: 4;
            margin: -5px 0 0 -9px;
        }
    }

    .bx-wrapper .bx-pager.bx-default-pager a {
        background: #666;
        text-indent: -9999px;
        display: block;
        width: 8px;
        height: 8px;
        margin: 0 10px;
        outline: 0;
        border-radius: 5px;
        background: $white;
        opacity: 0.4;
        position: relative;
    }

    .bx-wrapper .bx-pager.bx-default-pager a:hover,
    .bx-wrapper .bx-pager.bx-default-pager a.active {
        background: $white;
        opacity: 1;
        position: relative;
        display: block;

    }


    /* DIRECTION CONTROLS (NEXT / PREV) */

    .bx-wrapper .bx-prev {
        left: 0px;
        background: url(../img/content/main_slider_control.png)no-repeat;
        transform: rotate(180deg);
    }

    .bx-wrapper .bx-next {
        right: 27px;
        background: url(../img/content/main_slider_control.png)no-repeat;
    }


    .bx-wrapper .bx-controls-direction a {
        position: absolute;
        top: 36%;
        //right: 24px;
        outline: 0;
        width: 50px;
        height: 50px;
        text-indent: -50px;
        z-index: 9999;
        color: transparent;
        opacity: 0.3;
        transition: $transition;
        &:hover{
            opacity: 1;
            transition: $transition;
        }
    }

    .bx-wrapper .bx-controls-direction a.disabled {
        display: none;
    }
}


